import styled from "styled-components"

export const ButtonDiv = styled.div`
  margin: 0 auto 1rem;
`

export const StartButton = styled.button`
  padding: 1rem;
  background-color: #f79393;
  font-size: 1rem;
  color: #fff;
  border: none;
  transition: 0.2s all;

  &:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
`
